import { Component } from 'preact';

export default class LocationService extends Component {
	_handlePhoneClick = (event) => {
		console.log('Preact received event from phone:', event);
	};

	constructor (props) {
		super(props);
		this.state = {
			address: props.address,
			authToken: props.authToken,
			locality: props.locality,
			region: props.region,
			error: null,
			isLoaded: false,
			location: {
				store_info: {}
			}
		};
	}

	componentDidMount () {
		this.locationPhone.addEventListener('mfPhoneClicked', this._handlePhoneClick);

		fetch(`https://momentfeed-prod.apigee.net/api/llp.json?address=${this.state.address}&auth_token=${this.state.authToken}&locality=${this.state.locality}&multi_account=false&pageSize=30&region=${this.state.region}`)
			.then(res => res.json())
			.then(
				(result) => {
					this.setState({
						isLoaded: true,
						location: result[0]
					});
				},
				// Note: it's important to handle errors here
				// instead of a catch() block so that we don't swallow
				// exceptions from actual bugs in components.
				(error) => {
					this.setState({
						isLoaded: true,
						error
					});
				}
			);
	}

	componentWillUnmount() {
		this.locationPhone.removeEventListener('mfPhoneClicked', this._handlePhoneClick);
	}

	render () {
		return (
			<div>
				<location-header location-name={this.state.location.store_info.name} />
				<location-address location-address={this.state.location.store_info.address}
					location-locality={this.state.location.store_info.locality}
					location-region={this.state.location.store_info.region}
					location-zip={this.state.location.store_info.postcode}
					location-url={this.state.location.llp_url}
					use-ga={false}
				/>
				<location-phone location-id={this.state.location.momentfeed_venue_id}
					location-phone={this.state.location.store_info.phone}
					location-url={this.state.location.llp_url}
					use-ga={false}
					ref={elem => this.locationPhone = elem}
				/>
				<map-card longitude={this.state.location.store_info.longitude}
					latitude={this.state.location.store_info.latitude}
					map-pins="pin-l-1+bc1448"
					api-key="pk.eyJ1IjoibW9tZW50ZmVlZCIsImEiOiJPS21SYV80In0.rZ-uswLQiEx1hkfcj5fVhw"
					alt-text={this.state.location.store_info.name}
				/>
			</div>
		);
	}
}



